<template>
  <div class="col-9 bg-muted d-flex flex-column">
     <loading-overlay :loading="loadingOverlay" class="flex-fill overflow-h">
    <div class="d-flex flex-row-reverse">
      <button class="m-4 btn btn-primary" @click="showAddModal()" round>
        <i class="las la-plus-circle"></i>Add Tag
      </button>
    </div>
    <div class="row">
      <div class="col-6 p-2" v-for="tag in tagAndSubTagList" :key="tag.ID">
        <div class="tag-container px-2 py-3">
          <div>
            <span @click="showEditModal(tag)" class="tag px-3 py-2">{{tag.Name}}</span>
          </div>
          <div class="mt-3">
            <span class="stag px-2 py-1" @click="addSubTag(tag)">+ Add</span>
            <span
              v-for="stag in tag.SubTags"
              @click="showActionsModal(stag,tag)"
              class="stag px-2 py-1 m-1"
              :key="stag.ID"
            >{{stag.Name}}</span>
          </div>
        </div>
      </div>
    </div>
     </loading-overlay>
    <sub-tag-modal
      @addSubTag="appendSubTag"
      @updateSubTag="editSubTag"
      @deleteSubTag="removeSubTag"
    />
    <modal name="tagModal" height="auto" width="400" @before-open="beforeOpen" @opened="focus">
      <div class="form">
        <label>Tag Name</label>
        <input
          placeholder=" Enter Tag Name"
          type="text"
          v-model="tagName"
          class="textbox form-control"
        />
        <div class="d-flex flex-row-reverse justify-content-between align-items-center">
          <button class="m-2 btn btn-primary" v-if="btnTag == 'Add'" @click="addTag">Add</button>

          <button class="m-2 btn btn-primary" v-if="btnTag == 'Update'" @click="updateTag">Update</button>
          <a v-if="btnTag == 'Update'" @click="removeTag" class="text-danger mr-2">
            <i class="lar la-trash-alt"></i> Delete
          </a>
        </div>
      </div>
    </modal>
  </div>
</template>
<script>
import axios from "axios";
import SubTagModal from "./Tag/SubTagModal";
import LoadingOverlay from "@/components/Loaders/LoadingOverlay";
export default {
  components: {
    SubTagModal,
    LoadingOverlay
  },
  data() {
    return {
      subTag: "",
      tagName: "",
      subTagName: "",
      tagAndSubTagList: [],
      subTagList: [],
      tagID: 0,
      subTagID: 0,
      btnTag: "",
      btnSubTag: "",
      selected: "",
      btnAdd: false,
      parentTag: {},
      loadingOverlay:false
    };
  },

  methods: {
    addSubTag(tag) {
      this.$modal.show("subtag", {
        parentTag: tag
      });
    },
    showActionsModal(stag, tag) {
      this.$modal.show("subtag", {
        parentTag: tag,
        subTag: stag
      });
    },
    showAddModal() {
      this.tagName = "";
      this.btnTag = "Add";
      this.$modal.show("tagModal");
    },
    showEditModal(tag) {
      this.tagID = tag.ID;
      this.tagName = tag.Name;
      this.btnTag = "Update";
      this.$modal.show("tagModal");
    },
    add() {},

    async getTagsAndSubTags() {
      try {
        // let loader = this.$loading.show({
        //   loader: "dots"
        // });
        this.loadingOverlay=true;
        const res = await axios
          .get("TagsAndSubtags")
          .then(res => {
            this.tagAndSubTagList = res.data;
          })
          .finally(() => {
            this.loadingOverlay=false;
           // loader.hide();
          });
      } catch {
        return null;
      }
    },

    async addTag() {
      if (!this.tagName) return;
      if (this.exists) return;
      try {
        let loader = this.$loading.show({
          loader: "dots"
        });
        const res = await axios
          .post("addTag", {
            Name: this.tagName
          })
          .then(res => {
            this.$modal.hide("tagModal");
            this.getTagsAndSubTags();
          })
          .finally(() => {
            loader.hide();
          });
      } catch {
        return null;
      }
    },

    async updateTag() {
      if (!this.tagName) return;
      if (this.exists) return;
      try {
        const res = await axios.post("updateTag", {
          ID: this.tagID,
          Name: this.tagName
        });
        this.$modal.hide("tagModal");
        this.getTagsAndSubTags();
      } catch {
        return null;
      }
    },

    async removeTag() {
      try {
        const res = await axios.post("removeTag", {
          ID: this.tagID
        });
        this.$modal.hide("tagModal");
        this.getTagsAndSubTags();
      } catch {
        return null;
      }
    },

    focus(modal) {
      if (modal == "subtag") {
        this.$refs.subtagInput.focus();
      }
    },
    appendSubTag(sTag) {
      this.tagAndSubTagList.forEach(x => {
        if (x.ID === sTag.TagID) {
          x.SubTags.push(sTag);
        }
      });
    },
    editSubTag(sTag) {
      let index = this.tagAndSubTagList.findIndex(x => x.ID == sTag.TagID);
      let subItem = this.tagAndSubTagList[index].SubTags.filter(
        x => x.ID == sTag.ID
      );
      subItem[0].Name = sTag.Name;
    },
    removeSubTag(sTag) {
      let index = this.tagAndSubTagList.findIndex(x => x.ID == sTag.TagID);
      let i = this.tagAndSubTagList[index].SubTags.findIndex(
        x => x.ID == sTag.ID
      );
      this.tagAndSubTagList[index].SubTags.splice(i, 1);
    }
  },
  mounted() {
    this.getTagsAndSubTags();
  },
  computed: {
    exists() {
      return this.tagAndSubTagList.map(x => x.Name).includes(this.tagName);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "~bootstrap/scss/bootstrap.scss";
.subtag {
  display: flex;
  justify-content: center;
  padding: 5px;
  width: 25%;
  height: 50px;
  margin: 10px;
  font-size: 20px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.7em;
  color: $secondary;
  font-weight: 300;
  margin-top: 10px;
}
.button {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 30%;
  padding-top: 20px;
  padding-left: 80%;
}
.form {
  padding: 30px;
  margin: 10px;
}
.textbox {
  padding: 0;
  margin-bottom: 10px;
  display: block;
}
label {
  margin-bottom: 0px;
  color: #385898;
  font-weight: bold;
  font-size: 15px;
  text-align: left;
}
.confirm {
  float: right;
  padding-top: 15px;
  padding-right: 0;
  padding-bottom: 30px;
}
.v--modal-box {
  margin-top: 10% !important;
}
.style-chooser .vs__search::placeholder,
.style-chooser .vs__dropdown-toggle,
.style-chooser .vs__dropdown-menu {
  max-height: 100px;
}

.custom-avatar.avatar table[data-v-4ffd1741] {
  width: 40px;
  height: 40px;
}
</style>
<style lang="scss">
.tag-container {
  border-radius: 18px;
  background-color: #fff;
}

.tag {
  background-color: #e2e2e2;
  border-radius: 18px;
  font-weight: 600;
  font-size: 0.9em;
  letter-spacing: 0.5px;
}
.stag {
  cursor: pointer;
  border: 1px solid #e2e2e2;
  font-size: 0.8em;
  border-radius: 18px;
  letter-spacing: 0.5px;
  font-weight: 600;
  display: inline-block;
  transition: 0.3s;
  &:hover {
    background-color: #e2e2e2;
  }
  &:active {
    background-color: #888;
  }
  &:first-child {
    color: var(--primary);
    &:hover {
      color: #fff;
      background-color: var(--primary);
      border-color: var(--primary);
    }
  }
}

.bg-muted {
  background-color: #eeeded;
}
</style>
