<template>
  <modal name="subtag" height="auto" width="400" @before-open="beforeOpen" @opened="focus">
    <loading-overlay white :loading="loading">
      <div class="p-2">
        <template>
          <div class="mb-2">
            <span class="tag px-3 py-2">{{parentTag.Name}}</span>
          </div>
          <small class="text-muted">Sub Tag Name</small>
          <input ref="input" class="form-control" v-model="input" />
          <div class="d-flex flex-row-reverse justify-content-between align-items-center">
            <button v-if="editModal" class="btn btn-primary my-2 btn-sm" @click="update">Update</button>

            <a v-if="editModal" @click="remove" class="text-danger mr-2">
              <i class="lar la-trash-alt"></i> Delete
            </a>
            <button v-else class="btn btn-primary my-2 btn-sm" @click="add">Add Sub Tag</button>
          </div>
        </template>
      </div>
    </loading-overlay>
  </modal>
</template>
<script>
import axios from "axios";
import LoadingOverlay from "@/components/Loaders/LoadingOverlay";
export default {
  components: {
    LoadingOverlay
  },
  data() {
    return {
      input: "",
      parentTag: {},
      subTag: {},
      loading: false
    };
  },
  methods: {
    resetModal() {
      this.input = "";
      this.subTag = {};
    },
    beforeOpen(event) {
      this.resetModal();

      let { parentTag, editModal, subTag } = event.params;
      this.parentTag = parentTag;
      if (editModal) this.editModal = editModal;
      if (subTag) {
        this.subTag = subTag;
        this.input = subTag.Name;
      }
    },
    focus() {
      this.$refs.input.focus();
    },
    update() {
      if (!this.input) return;
      if (this.exists) return;
      this.loading = true;

      axios
        .post("updateSubtag", {
          ID: this.subTag.ID,
          Name: this.input,
          TagID: this.parentTag.ID
        })
        .then(res => {
          if (res.status == 200) {
            this.$emit("updateSubTag", res.data);
          }
        })
        .catch()
        .finally(res => {
          this.loading = false;
          this.$modal.hide("subtag");
        });
    },
    remove() {
      this.loading = true;

      axios
        .post("removeSubtag", {
          ID: this.subTag.ID,
          Name: this.input,
          TagID: this.parentTag.ID
        })
        .then(res => {
          if (res.status == 200) {
            this.$emit("deleteSubTag", res.data);
          }
        })
        .catch()
        .finally(res => {
          this.loading = false;
          this.$modal.hide("subtag");
        });
    },
    add() {
      if (!this.input) return;
      if (this.exists) return;

      this.loading = true;
      axios
        .post("addSubtag", {
          Name: this.input,
          TagID: this.parentTag.ID
        })
        .then(res => {
          if (res.status == 200) {
            this.$emit("addSubTag", res.data);
          }
        })
        .catch()
        .finally(res => {
          this.loading = false;
          this.$modal.hide("subtag");
        });
    }
  },
  computed: {
    exists() {
      return this.parentTag.SubTags.map(x => x.Name).includes(this.input);
    },
    editModal() {
      return this.subTag.ID;
    }
  }
};
</script>
